import React, {useState, useEffect} from 'react';
import blogService from './services/blogs'

function App() {

  const [blogs, setBlogs] = useState([])
  const [title, setTitle] = useState("")
  const [author, setAuthor] = useState("")
  const [url, setUrl] = useState("")
  const [likes, setLikes] = useState("")

  useEffect(() => {
    blogService
      .getAll()
        .then(initialNotes => {
          setBlogs(initialNotes)
        })
  }, [])

  const lueTiedot = () => blogs.map(blog => 
    <li>
      {blog.author}
    </li>
  )

  const addBlog = (event) => {
    event.preventDefault()
    const blogObject = {
      title: title,
      author: author,
      url: url,
      likes: likes
    }

    blogService
      .create(blogObject)
      .then(response => {
        setBlogs(blogs.concat(response.data))
      })
  }

  const titleHandler = (event) => {
    setTitle(event.target.value)
  }

  const authorHandler = (event) => {
    setAuthor(event.target.value)
  }

  const urlHandler = (event) => {
    setUrl(event.target.value)
  }

  const likeHandler = (event) => {
    setLikes(event.target.value)
  }

  return (
    <div>
      <form onSubmit = {addBlog}>
        <h1>Tallenna blogien tiedot:</h1>
        <label>Title:<br/>
          <input value = {title} onChange = {titleHandler}></input><br/>
        </label>
        <label value = {author} onChange = {authorHandler}>Author:<br/>
          <input></input><br/>
        </label>
        <label>Url:<br/>
          <input value = {url} onChange = {urlHandler}></input><br/>
        </label>
        <label value = {likes} onChange = {likeHandler}>Likes:<br/>
          <input></input>
        </label><br/><br/>
        <button type = "submit">Lisaa blogi</button>
      </form><br/>
      
      {title}<br/>
      {author}<br/>
      {url}<br/>
      {likes}

      <div>
        {lueTiedot()}
      </div>
      
    </div>
    
  );
}

export default App;
